
html,body,h1,h2,ul{margin:0;padding:0
}
body{font-size:0;background-color:#ececec
}
#app{color:#35495e;font-family:"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;font-size:16px
}
code{-webkit-border-radius:2px;border-radius:2px;font-family:Menlo,Monaco,Consolas,Courier,monospace;font-size:.9em;-webkit-font-smoothing:initial;-moz-osx-font-smoothing:initial;word-break:break-all
}
code.z-code{color:#e96900;padding:3px 5px;margin:0 2px;background-color:#f8f8f8
}
.z-backtop-wrapper{color:#41b883
}
.heart{color:red
}
.clearfix{*zoom:1;clear:both
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden;zoom:1
}
h2{color:#41b883;margin-bottom:24px
}
h3{margin:1.6em 0 .6em 0
}
a{color:#41b883;text-decoration:none
}
.header{text-align:center
}
.navbar.z-navbar-wraper .z-navbar-fix{z-index:900
}
#SOHUCS{width:92% !important;margin:0 auto 50px !important;padding:10px !important;-webkit-box-sizing:border-box !important;box-sizing:border-box !important;background-color:#fff !important
}
.content{padding:20px 0;margin:50px 0 10px;width:92%;background-color:#fff;-webkit-border-radius:6px;border-radius:6px;-webkit-box-sizing:border-box;box-sizing:border-box
}
.content .siderbar-wrapper .z-dimmer{z-index:8
}
.content .siderbar-wrapper .z-aside.z-aside-left{width:180px;z-index:9
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content{padding:60px 0 44px 0
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar{width:180px;border-right:none
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar>a{padding:0 8px 0 16px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar>.group-title{padding:8px 8px 8px 16px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar ul li a{padding-left:24px;padding-right:8px
}
.content .siderbar{width:16.66666667%;float:left;border-right:1px solid #eee
}
.content .siderbar .router-link-active{color:#41b883;border-right:2px solid #41b883;background-color:rgba(65,184,131,0.3)
}
.content .siderbar a{display:block
}
.content .siderbar>a{color:#35495e;height:42px;line-height:42px;padding:0 16px 0 32px
}
.content .siderbar>a .group-title{padding:0;white-space:nowrap;overflow:hidden;text-overflow:ellipsis
}
.content .siderbar .group-title{display:inline-block;padding:8px 16px 8px 32px
}
.content .siderbar>.group-title{height:26px;line-height:26px
}
.content .siderbar ul{list-style:none
}
.content .siderbar ul li a{color:gray;height:42px;line-height:42px;padding-left:48px;padding-right:16px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;font-size:14px
}
.content .siderbar ul li a:hover{color:#41b883
}
.content .section{padding:5px 4%;margin-left:16.66666667%;border-left:1px solid #eee
}
.content .section section{max-width:800px;margin:0 auto
}
@media screen and (max-width:1366px){
.content .siderbar{width:210px
}
.content .section{margin-left:210px
}
}
@media screen and (max-width:768px){
#app{font-size:14px
}
#SOHUCS{width:100% !important;margin:0 0 10px 0 !important
}
.content{width:100%;margin:10px 0
}
.content>.siderbar{display:none
}
.content .section{margin-left:0;padding:10px
}
.content .siderbar-wrapper .z-aside.z-aside-left{width:180px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content{padding:60px 0 44px 0
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar{width:180px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar>a{padding:0 8px 0 16px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar>.group-title{padding:8px 8px 8px 16px
}
.content .siderbar-wrapper .z-aside.z-aside-left .z-aside-content .siderbar ul li a{padding-left:24px;padding-right:8px
}
.z-navbar-wraper .z-navbar .small-logo{display:none
}
.z-navbar-wraper .z-navbar .z-guide{display:block;color:#41b883;font-weight:bold;cursor:pointer
}
.z-navbar>ul{padding:10px
}
.z-backtop-wrapper.show-top{right:10px;bottom:10px
}
}
@media screen and (min-width:768px){
.main{text-align:center
}
.content{display:inline-block;text-align:left
}
.z-navbar>ul{padding:10px 60px
}
}
.footer{text-align:center;color:#fff;padding:50px 0;background-color:#727f80;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei",SimSun,sans-serif
}
.footer a{color:#fff
}
.logo{width:100px;height:100px
}
.introduce{margin:10px 5px 0 0;display:inline-block
}
.introduce:last-child{margin-right:0
}
.introduce-block{margin:10px 0 0
}
.z-navbar>ul{height:40px;line-height:40px
}
.z-navbar>ul>li{list-style:none;float:right;margin:0 .6em
}
.z-navbar>ul>li>a{color:#34495e;padding-bottom:3px
}
.z-navbar>ul>li>a:hover{border-bottom:3px solid #41b883
}
.z-navbar .small-logo{float:left;height:40px
}
.z-navbar .small-logo a{display:inline-block;height:40px
}
.z-navbar .small-logo a span{display:inline-block;height:100%;margin-left:10px;font-size:32px;float:right
}
.z-navbar .small-logo a:hover{border-bottom:none
}
.z-navbar .small-logo img{max-height:40px
}
.z-navbar .z-guide{float:left;position:relative;display:none
}
.z-navbar .z-guide .z-dropmenu-title>a{color:#41b883;font-weight:bold
}


